import React, { FC } from 'react';
import { CarouselProvider, Slide, Slider } from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';

import { CarouselButtons } from './CarouselButtons';
import { DotGroup } from './DotGroup';

import { homePageImages } from '@static/data';
import { Image } from '@components/generic';

export const Carousel: FC = () => {
  const totalSlides = homePageImages.length;

  return (
    <CarouselProvider
      naturalSlideWidth={100}
      naturalSlideHeight={100}
      totalSlides={totalSlides}
      isIntrinsicHeight
      isPlaying
    >
      <div className={'relative'}>
        <Slider>
          {homePageImages.map(({ src, alt }, index) => (
            <Slide key={index} index={index}>
              <div
                style={{
                  width: '100%',
                  height: '60vh',
                  minHeight: '275px',
                  maxHeight: '600px',
                }}
              >
                <Image
                  src={src}
                  alt={alt}
                  fallbackSrc={`https://via.placeholder.com/1000?text=Lab+Image+${
                    index + 1
                  }`}
                />
              </div>
            </Slide>
          ))}
        </Slider>

        <div
          className={'w-full absolute'}
          style={{ top: '50%', transform: 'translateY(-50%)' }}
        >
          <CarouselButtons />
        </div>

        <div
          className={'w-full absolute flex justify-center my-2'}
          style={{ bottom: '0.5rem' }}
        >
          <DotGroup slides={homePageImages} />
        </div>
      </div>
    </CarouselProvider>
  );
};
