import rawImages from './home-page-images.json';
import { Image } from '../../../types/image';

export const homePageImages: Image[] = Object.entries(rawImages)
  .map(([key, value]) => ({
    id: Number(key),
    // @ts-ignore
    src: value.image,
    // @ts-ignore
    alt: value.name,
  }))
  .sort((a, b) => a.id - b.id);
